import React from 'react';
import { connect } from 'react-redux';
import { IPromotionItem } from '../types';


class PromotionItem extends React.Component<IPromotionItem, IPromotionItem> {
    render() {
        const {
            photoUrl,
            name,
            description,
            price,
            discountPrice,
            discount
        } = this.props

        return (
            <div className="PromotionItem">
                <div className="imageBlock">
                    <img src={photoUrl} alt="photoUrl" className="image"/>
                    <p className="discount">-{discount}%</p>
                </div>
                <h1 className="name">{name}</h1>
                <h3 className="description">{description}</h3>
                <div className="priceBlock">
                    <p className="price">{price}</p>
                    <p className="discountPrice">{discountPrice}</p>
                </div>
            </div>
        )
    }
}


export default connect()(PromotionItem)