import React from 'react';
import { connect } from 'react-redux';

export interface SearchListItemProps {
    id:number,
    index:number,
    name:string,
    warehouse_id:number,
    photoUrl:string,
    price:number,
    discount:number,
    remain:number
}

export interface SearchListItemState {
    id:number,
    index:number,
    name:string,
    warehouseId:number,
    photoUrl:string,
    price:number,
    discount:number,
    remain:number
}

const mapStateToProps = (reducer:any, other:any) => {
    return {
        ...other
    }
}

class SearchListItemComponent extends React.Component<SearchListItemProps, SearchListItemState> {

    render() {
        console.log("RENDER SEARCH LIST ITEM", this.props)
        const {
            id, index, name, warehouse_id, photoUrl, price,
            discount, remain
        } = this.props
        return (
            <tr key={id}>
                <td>{name}</td>
                <td>{photoUrl}</td>
                <td>{warehouse_id}</td>
                <td>{remain}</td>
                <td>{Date.now()}</td>
            </tr>
        )
    }
}

const SearchListItem = connect()(SearchListItemComponent)

export default SearchListItem