class SearchFormHome extends React.Component<ISearchForm, ISearchForm> {

    constructor(props:ISearchForm, state:ISearchForm) {
        super(props, state)

        this.state = {
            ...props
        }

        this.handleSearchSubmit = this.handleSearchSubmit.bind(this)
        this.handleSearchFieldChange = this.handleSearchFieldChange.bind(this)
    }


    handleSearchSubmit(event:React.MouseEvent<HTMLButtonElement>):void {
        event.preventDefault()
        const { searchInput } = this.state
        const { action } = this.props
        action(searchInput)
    }

    handleSearchFieldChange(event:React.ChangeEvent<HTMLInputElement>):void {
        event.preventDefault()
        this.setState({
            searchInput: event.target.value
        })
    }

    render() {
        const { searchInput, searchFormSubmitted } = this.props
        return (
            <div className="SearchFormHome">
                <div className="input-group standart-container">
                    <input
                        type="text"
                        id="searchInput"
                        value={searchInput}
                        className="form-control"
                        aria-label="Sarch in warehouses"
                        onChange={this.handleSearchFieldChange}
                        placeholder="Введіть назву товару"
                    />
                    <select className="custom-select" id="inputGroupSelect04" aria-label="Example select with button addon">
                        <option selected>У всіх Аптеках</option>
                        <option value="1">Аптека №1</option>
                        <option value="2">Аптека №2</option>
                        <option value="3">Аптека №3</option>
                    </select>
                    <div className="input-group-append">
                        <ActionButton
                            text="Шукати"
                            classList={["default-button"]}
                            action={() => {console.log("Home search click")}}
                            iconName=""
                            iconSvgSrc=""
                        />
                    </div>
                </div>
            </div>
        )
    }
}

export default connect()(SearchFormHome)
