import React from 'react';
import { connect } from 'react-redux';
import ActionButton from './ActionButton';

interface TopNavBarData {
    phonesNumbers:string[],
    isUserAuth:boolean,
    cartOrdersCount:number
}

const mapStateToProps = (reducer:any) => {
    const { topNavBarState } = reducer
    return {
        ...topNavBarState
    }
}

const renderLoginRegister = () => {
    return (
        <div className="authBlock">
            <ActionButton
                text={"Увійти"}
                iconName=""
                action={() => {console.log("Login click")}}
                classList={["svg-icon"]}
                iconSvgSrc="/svg/user.svg"
            />
            <ActionButton
                text={"Зареєструватися"}
                iconName=""
                action={() => {console.log("Register click")}}
                classList={["svg-icon"]}
                iconSvgSrc="/svg/user.svg"
            />
        </div>
    )
}

const renderUserStatus = (name:string) => {
    return (
        <div className="authBlock">
            <div className="user-status">Hello user:{name}</div>
        </div>
    )
}

const renderAuth = (isUserAuth:boolean) => {
    if (isUserAuth)
        return renderUserStatus("Ostap")
    else
        return renderLoginRegister()
}

const TopNavBar = ({phonesNumbers, isUserAuth, cartOrdersCount}:TopNavBarData) => {
    return (
        <div className="TopNavBar">
            <div className="left-block TopNavBar-block">
                <div className="phones">
                    <span>Аптечна довідка:</span>
                    {phonesNumbers.map((phone, index) => {
                        return (
                            <span className="phoneNumber" key={index}>{phone}</span>
                        )
                    })}
                </div>
            </div>
            <div className="right-block TopNavBar-block">
                {renderAuth(isUserAuth)}
                <ActionButton
                    text={`Ваш кошик: ${cartOrdersCount} товарів`}
                    iconName=""
                    iconSvgSrc="/svg/basket.svg"
                    action={() => {console.log("Cart click")}}
                    classList={["cart", "svg-icon"]}
                />
            </div>
        </div>
    )
}

export default connect(mapStateToProps)(TopNavBar)