import { 
  INIT_PRODUCTS_SEARCH,
  SET_PRODUCTS_SEARCH,
  SET_PRODUCTS_SEARCH_SUCCESS,
  SET_PRODUCTS_SEARCH_FAIL,
  SET_PRODUCTS_FAKE
} from "../actions/types";

import {
  searchInitState,
  homePageState
} from './initState';

import { IHomePage, ISearchState } from "../types";

export const DefaultReducer = (state = {}, action:any) => {
  return state
}

export const HomeReducer = (state = homePageState, action:any):IHomePage => {
  if (action.type === "VOID") {    
    return {
      ...state
    }
  }

  return state
}

export const SearchReducer = (state = searchInitState, action:any):ISearchState => {

  if (action.type === INIT_PRODUCTS_SEARCH) {    
    return {
      ...state
    };
  }

  if (action.type === SET_PRODUCTS_FAKE) {    
    const { searchFormState } = state
    const { message, code, data, total } = action.payload
    return {
      ...state,
      productsRequestState: {
        message:message,
        products:[{
          id: 1,
          index: 1,
          name: action.payload,
          photo_url: "http://URL",
          warehouse_id: 9,
          price: 100,
          discount: 15,
          remain: 102
        }],
        isRequestSended:true,
        isResponseRecieved:true,
        error:"",
        code:0,
        total:1
      },
      searchFormState: {
        ...searchFormState,
        searchFormSubmitted:false,
        searchInput:""
      },
    }
  }

  if (action.type === SET_PRODUCTS_SEARCH) {
    console.log("SET_PRODUCTS_SEARCH_SUCCESS", state, action)
    const { productsRequestState, searchFormState } = state

    return {
      ...state,
      productsRequestState: {
        ...productsRequestState,
        isRequestSended:true,
        isResponseRecieved:false,
      },
      searchFormState: {
        ...searchFormState,
        searchFormSubmitted:true,
        searchInput:"",
      },
    }
  }

  if (action.type === SET_PRODUCTS_SEARCH_SUCCESS) {
    console.log("SET_PRODUCTS_SEARCH_SUCCESS", state, action)
    const { searchFormState } = state
    const { message, code, data, total } = action.payload
    return {
      ...state,
      productsRequestState: {
        message:message,
        products:data,
        isRequestSended:true,
        isResponseRecieved:true,
        error:"",
        code:code,
        total:total
      },
      searchFormState: {
        ...searchFormState,
        searchFormSubmitted:false,
        searchInput:""
      },
    }
  }

  if (action.type === SET_PRODUCTS_SEARCH_FAIL) {
    console.log("SET_PRODUCTS_SEARCH_FAIL", state, action)
    const { searchFormState } = state
    const { message, code, error } = action.payload
    return {
      ...state,
      productsRequestState: {
        message:message,
        products:[],
        isRequestSended:true,
        isResponseRecieved:true,
        error:error,
        code:code,
        total:0
      },
      searchFormState: {
        ...searchFormState,
        searchFormSubmitted:false,
        searchInput:""
      },
    }
  }

  return state;
}

export default DefaultReducer
